import { StyledWrapper, Caption } from './HeroMedia.styles';
import propTypes from './HeroMedia.types';

const HeroMedia = ({ media, caption }) => {
  return (
    <StyledWrapper>
      {media}
      {caption && <Caption>{caption}</Caption>}
    </StyledWrapper>
  );
};

HeroMedia.propTypes = propTypes;

export default HeroMedia;
